import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/styles';
import React from 'react';

import SitePage from 'components/SitePage';
import WritingIcon from 'components/WritingIcon';

import poems from './poems';
import styles from './Writing.css';

const TITLE = `Writing - Cedric McDougal`;
const DESCRIPTION = `TODO`;

const Writing = () => {
  const classes = makeStyles(styles)();

  return (
    <SitePage
      description={DESCRIPTION}
      favicon="writing"
      icon={WritingIcon}
      mobileTitle="Writing"
      title={TITLE}>
      <div>
        {poems.map(({ title, body }) => {
          return (
            <Paper key={title} className={classes.poem}>
              <Typography className={classes.poemTitle} variant="h6">
                {title}
              </Typography>
              <Typography className={classes.poemBody} variant="body2">
                {body}
              </Typography>
            </Paper>
          );
        })}
      </div>
    </SitePage>
  );
};

Writing.propTypes = {};

export default Writing;
